import Task from './Task';

const taskInput = document.querySelector("input[name='taskInput']");
const shoppingListDiv = document.querySelector('.shopping-list');
const completedDiv = document.querySelector('.completed');
const itemEl = document.querySelector('.shopping-list > .item');

let shoppingList = [];

const renderList = () => {
  const domNodes = shoppingList.map((item) => {
    return Task(item.task, item.priority, item.id);
  });

  shoppingListDiv.innerHTML = domNodes.join('');
};

taskInput.addEventListener('keyup', function (evt) {
  if (evt.key === 'Enter') {
    const taskId = `${parseInt(
      Math.random() * 100000000
    )}-${new Date().getTime()}`;

    shoppingList.push({
      task: evt.target.value,
      id: taskId,
    });
    renderList();
    this.value = '';
  }
});

shoppingListDiv.addEventListener('click', function (evt) {
  if (evt.target.classList.contains('item')) {
    const item = evt.target;
    alert(item.getAttribute('data-id'));
  }

  if (evt.target.parentElement.classList.contains('priority-control')) {
    const priority = evt.target.classList.value;
    const taskId = evt.target.parentElement.parentElement.getAttribute(
      'data-id'
    );
    shoppingList = shoppingList.map((item) => {
      if ((item.id = taskId)) {
        return {
          ...item,
          priority,
        };
      }

      return item;
    });

    renderList();
  }

  if (evt.target.classList.contains('status')) {
    const taskId = evt.target.parentElement.getAttribute('data-id');
    console.log(taskId);
  }
});
